.form__input {
	padding: 0 0 10px 0;
	outline: 0;
	border: 0;
	box-sizing: border-box;
	min-height: 27px;
	font-weight: 400;
	font-size: 14px;
	line-height: calc(17/14);
	border-bottom: 1px solid rgba(0, 0, 0, .2);
}

.form__input:focus {
	box-shadow: 0 0 3px 2px rgba(56, 68, 68, 0.3);
}